
import { Box, Heading,Text,FormControl,FormLabel, Input } from "@chakra-ui/react"
import { Link } from "react-router-dom"
import { useRef } from "react";
import emailjs from '@emailjs/browser';

export const Contact=()=>{
    const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_98wg5h9', 'template_g97t89j', form.current, 'NDFlDk_dFPHEsJ9Wd')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
  };
    return(
    <Box bgColor="#111111" height='100vh'>
        <Box>
            <Heading color='white'>Contact Me</Heading>
        </Box>
        <Box display={{base:'block',md:'block',lg:'flex'}} width='60%' margin='auto' border='1px solid white' justifyContent='space-around' mt='100px' p='20px'>
            <Box color='white' display='flex' flexDirection='column' textAlign='start' pl='5%' gap='20px'>
                <Text>Get in Touch</Text>
                <Link>kunwarkartik@gmail.com</Link>
                <Link>Linkedin</Link>
                <Link>GitHub</Link>
                <Text>+91 7060950662</Text>
            </Box>
            <Box color='white' display='flex' flexDirection='coloumn' justifyContent='center' >
                <FormControl ref={form} onSubmit={sendEmail}>
                    <FormLabel>Name</FormLabel>
                    <Input type="text" name="user_name" />
                    <FormLabel>Email</FormLabel>
                    <Input type="email" name="user_email" />
                    <FormLabel>Message</FormLabel>
                    <textarea name="message" className="msg" />
                    <Input type="submit" value="Send" />
                </FormControl>
            </Box>
        </Box>
    </Box>
    
    )
}